.body{
  padding-bottom: 40px;
}

.container{
  display: flex;
  flex-direction: column;
  margin: auto;
  margin-top: 200px;
  background: #fff;
  width: 600px;
  padding-bottom: 30px;
  border-radius: 75px;
}

.header{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  width: 100%;
  margin-top: 30px;
}

.text{
  color: black;
  font-size: 48px;
  font-weight: 700;

}

.underline{
  width: 61px;
  height: 6px;
  background: #6394F8;
  border-radius: 9px;
}

.inputs{
  margin-top: 55px;
  display: flex;
  flex-direction: column;
  gap:25px;
}

.input{
  display: flex;
  align-items: center;
  margin: auto;
  width: 480px;
  height: 80px;
  background: #eaeaea;
  border-radius: 6px;
}

.input img{
  margin: 0px 30px;
}

.input input{
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  background: transparent;
  color: #797979; 
  font-size: 20px;
  font-weight: 500;
} 

.submit-container{
  display: flex;
  gap: 30px;
  margin: 30px auto;
}

.submit{

  display: flex;
  justify-content: center;
  align-items: center;
  width: 220px;
  height: 50px;
  color: #fff;
  background: #6394F8;
  border-radius: 50px;
  font-size: 20px;
  font-weight: 700;
  cursor: pointer;
}

.gray{
  background: #eaeaea;
  color: #797979;
}

@media (max-width:430px) {
  .container{
    width: 400px;
    margin-top: 50px;
  }

  .input{
    width: 350px;
  }

  .submit{
    width: 120px;
  }
  .body{
    padding-bottom: 50px;
  }
}